import axios from 'axios'


import settle from "axios/lib/core/settle";
import buildURL from "axios/lib/helpers/buildURL";
import buildFullPath from "axios/lib/core/buildFullPath";

import store from '@/store/index.js'

import {
	baseURL
} from '@/config/index.js'

//uniapp axiosadapter is not a function
axios.defaults.adapter = function(config) {
	return new Promise((resolve, reject) => {
		const fullurl = buildFullPath(config.baseURL, config.url);

		let header = config.headers
		header.token = 'xxxxxx'
		// console.log(config.headers, 'header')
		uni.request({
			method: config.method.toUpperCase(),
			url: buildURL(fullurl, config.params, config.paramsSerializer),
			header,
			data: config.data,
			dataType: config.dataType,
			responseType: config.responseType,
			sslVerify: config.sslVerify,
			complete: function complete(response) {

				response = {
					data: response.data,
					status: response.statusCode,
					errMsg: response.errMsg,
					header: response.header,
					config,
				};

				settle(resolve, reject, response);
			},
		});
	});
};




// axios
const request = axios.create({
	baseURL: baseURL, // api  base_url
	timeout: 60000 // 
})



// request
request.interceptors.request.use(
	config => {


		if (store.getters.token) {
			// Authorization
			// console.log(store.getters.token)

			// config.headers["token"] = 'xxx';
			// console.log(config.headers)
		}
		config.headers["token"] = 'xxx';
		console.log(config.headers,'headers')
		return config;
	},
	error => {

		return Promise.reject(error);
	}
);
// response 
request.interceptors.response.use(
	(response) => {
		return new Promise((resolve, reject) => {
			const res = response.data
			// 
			resolve(response.data)
		})
	},
	(error) => {
		console.log(error)
		uni.hideToast();
		uni.showToast({
			title: ',',
			duration: 2000,
			icon: 'none'
		});
		Promise.reject(error)
		return ''
	}
)





export default request