/**
 *  @description requesttoken
 *  @param config config (url/appid)
 *  @param getToken getToken token
 *  @param setToken setToken token
 *  @param tool tool API(toast/confirm...)
 *  @param timeout timeout  
 *  @author Coding by Akai,Dear user,if you think this a litte uncertain,please add my WeChat to contact me;
 *  @description WeChat Akaibiu (,~!!!)
 *  @version 1.0.0 
 */
// import config from '@/config/config.js';
// import {getToken,setToken} from '@/utils/auth';
// import errorCode from '@/utils/errorCode';
// import tool from '@/utils/common.js';
import {
	baseURL
} from '@/config/index.js'
import {
	getToken,
	removeToken
} from '@/utils/auth.js'

import store from '@/store/index.js'

let timeout = 10000;
const baseUrl = baseURL;
const request = config => {
	/**
	 * @description  isTokentruetoken(tokentoken_type)  falsetoken
	 * @description  config.header['Authorization'] = 'Bearer ' + uni.getStorageSync('token'); // token_type 'Bearer ' 
	 * @description  config.header['Content-Type'] = 'application/x-www-form-urlencoded'; // 
	 */
	// const isToken = config.headers.isToken; 
	config.header = config.header || {};

	let token = ""
	if (store.getters.token) {
		token = store.getters.token
	} else if (getToken()) {
		store.commit('SET_TOKEN', token)
		token = store.getters.token
	}


	if (token) {
		// Authorization
		// console.log(store.getters.token)

		config.header["token"] = store.getters.token;
		// console.log(config.headers)
	}
	/**
	 * @description  getparams
	 */
	if (config.params) {
		let url = config.url + '?' + tool.tansParams(config.params)
		url = url.slice(0, -1)
		config.url = url

	}
	return new Promise((resolve, reject) => {
		uni.request({
			method: config.method || 'get',
			timeout: config.timeout || timeout,
			url: config.baseUrl || baseUrl + config.url,
			data: config.data,
			header: config.header,
			dataType: 'json',
			success(res) {
				if (res.data.code == 201) {

					removeToken()
					store.commit('SET_TOKEN', undefined)
					uni.$u.toast('')
					setTimeout(() => {
						uni.reLaunch({
							url: '/pages/login/index'
						});
					}, 1000)

					reject(res.data)
				}

			
				if (res.data.code == 1) {
					uni.$u.toast(res.data.data)
					reject(res.data)
				}

				// switch (res.data.code) {
				// 	case 401:
				// 		tool.toastTip(res.data.msg, 'none', 1500);
				// 		break;
				// 	case 500:
				// 		tool.toastTip(res.data.msg, 'none', 1500);
				// 		break;
				// 	default:
				// 		/*  */
				// 		break;
				// }
				/**
				 * @description 
				 */
				resolve(res.data)
			},
			fail: (error) => {
				console.log(error, 'xxx')
				// let {errMsg} = error
				// if (errMsg === 'Network Error') {
				// 	errMsg = ''
				// } else if (errMsg.includes('timeout')) {
				// 	errMsg = ''
				// } else if (errMsg.includes('Request failed with status code')) {
				// 	errMsg = '' + errMsg.substr(errMsg.length - 3) + ''
				// }
				// tool.toastTip(errMsg, 'none', 1500)
				/**
				 * @description 
				 */
				reject(error)
			},
			complete() {
				/**
				 * @description 
				 */
				// uni.hideLoading() 
			}
		})
	})
}

/**
 *  @description request
 */

export default request